import AutoComplete from "components/AutoComplete/AutoComplete";
import ButtonBox from "components/ButtonBox/ButtonBox";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import InputBox from "components/InputBox/InputBox";
import ReadOnlyTable, { IReadonlyTableRef } from "components/ReadOnlyTable";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import SystemDatePicker from "components/SystemDatePicker/SystemDatePicker";
import Text from "components/Text";
import ToastCustom from "components/Toast";
import { useTranslateText } from "hook/CustomHooks";
import { numberFormatter } from "locale";
import { Column } from "primereact/column";
import { DropdownChangeParams } from "primereact/dropdown";
import { ToastPositionType } from "primereact/toast";
import { ChangeEvent, useEffect, useRef, useState } from "react";
import { linkAuthRoute, linkBasePath } from "routes";
import ApplicantService from "services/ApplicantService";
import BankMasterDataService from "services/masterdata/Banks/BankMasterDataService";
import StateService from "services/masterdata/Location/State";
import OrgStructureBranchService from "services/masterdata/OrgStruct/Branch/Branch";
import ProductConfigProductService from "services/masterdata/ProductConfig/ProductConfigProductService";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";
import UserService from "services/masterdata/User/UserService";
import LoanService from "services/transaction/LoanService";
import SimpleReactValidator from "simple-react-validator";
import {
  convertDateToISO,
  currencyFormatToNumberForFilter,
  formatDate,
  getLocalStorage,
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
  parsedDateFromAPIValue,
  validateGetAPIResponse,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  DROPDOWN_STYLE_TYPE,
  INPUT_STYLE_TYPE,
  LOAN_STATUS_ITEM,
  LOCAL_STORAGE,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import {
  LINK_DDE_STAGE_AND_STATUS,
  LINK_DDE_STAGE_AND_STATUS_STAGES,
  LINK_LOAN_DETAIL,
} from "../../../../transactionRoutes";

interface IDropDownOptions {
  uid?: string;
  label: string;
  value: string;
  code?: string;
  name?: string;
}

interface IQCNonInitiatedAdvanceSearch {
  applicationNumber: {
    uid: string;
    name: string;
  };
  customerName: {
    uid: string;
    name: string;
  };
  daysPendingGreaterThan: string;
  daysPendingLessThan: string;
  salesManager: {
    uid: string;
    name: string;
  };
  creditManager: {
    uid: string;
    name: string;
  };
  salesAgent: {
    uid: string;
    name: string;
  };
  loanAmountGreaterThan: string;
  loanAmountLessThan: string;
  product: string;
  state: {
    uid: string;
    name: string;
  };
  loginDate: string;
  branch: {
    uid: string;
    name: string;
  };
  lendingType: string;
  bankFinancier: {
    uid: string;
    name: string;
  };
}

interface IQCNonInitiatedAdvanceSearchDropdownList {
  product?: IDropDownOptions[];
  lendingType?: IDropDownOptions[];
}

const initialAdvanceSearchObj: IQCNonInitiatedAdvanceSearch = {
  applicationNumber: {
    uid: "",
    name: "",
  },
  customerName: {
    uid: "",
    name: "",
  },
  salesManager: {
    uid: "",
    name: "",
  },
  creditManager: {
    uid: "",
    name: "",
  },
  salesAgent: {
    uid: "",
    name: "",
  },
  product: "",
  state: {
    uid: "",
    name: "",
  },
  branch: {
    uid: "",
    name: "",
  },
  lendingType: "",
  bankFinancier: {
    uid: "",
    name: "",
  },
  loginDate: "",
  loanAmountGreaterThan: "",
  loanAmountLessThan: "",
  daysPendingGreaterThan: "",
  daysPendingLessThan: "",
};

const PageQCNonInitiated = () => {
  /*
    -------------------------------------------------------
      States
    -------------------------------------------------------
  */
  const [advancedSearchValues, setAdvancedSearchValues] =
    useState<IQCNonInitiatedAdvanceSearch>(initialAdvanceSearchObj);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<any>([
    { name: "", uid: "", userId: "" },
  ]);
  const [dataTable, setDataTable] = useState({
    limit: 10,
    loading: false,
    totalRecords: 0,
    start: 0,
    search: `&qcStatus:null&loanStatus:nq=${LOAN_STATUS_ITEM.DRAFT}`,
    runCustomQuery: false,
    records: [],
  });
  const [productDropdownOptions, setProductDropdownOptions] = useState<IDropDownOptions[]>([]);
  const [lendingTypeDropdownOptions, setLendingTypeDropdownOptions] = useState<IDropDownOptions[]>(
    []
  );
  // error handling states
  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right"); //NOSONAR
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  /*
    -------------------------------------------------------
      refs
    -------------------------------------------------------
  */
  const dataTableRef = useRef<IReadonlyTableRef | null>(null);
  const toastMessageRef = useRef(null);

  /*
    -------------------------------------------------------
      Hooks: useEffect
    -------------------------------------------------------
  */

  useEffect(() => {
    init();
  }, []);

  useEffect(() => {
    if (dataTableRef && dataTableRef.current) {
      dataTableRef.current?.search();
    }
  }, [dataTable.search]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /* 
    ---------------------------------------------------------------------------------------------------
    Validators:
    ---------------------------------------------------------------------------------------------------
   */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;

    isValid = simpleValidator?.current?.allValid();
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  const init = async () => {
    getProductOptions();
    getLendingTypeOptions();
  };

  const getProductOptions = async () => {
    try {
      const res = await ProductConfigProductService.getAll(0, 30, "");
      if (validateGetAPIResponse(res)) {
        updateDropdownOptions("product", res?.data?.results);
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const getLendingTypeOptions = async () => {
    try {
      const res = await StaticMasterDataService.getAll(
        0,
        30,
        `staticMasterType.code=PRODUCT_LENDING_TYPE`
      );
      if (validateGetAPIResponse(res)) {
        updateDropdownOptions("lendingType", res?.data?.results);
      }
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  /*
    -------------------------------------------------------
      Handlers: Event handler for onChange, onBlur etc
    -------------------------------------------------------
  */

  const handleAdvanceSearchAutoCompleteChange = (event: any) => {
    const { name, value } = event.target;
    let fieldValue = "";
    if (typeof value === "object") {
      if (value.lanId) fieldValue = value.lanId;
      else if (value.fullName) fieldValue = value.fullName;
      else if (value.name) fieldValue = value.name;
      setAdvancedSearchValues((prev) => ({
        ...prev,
        [name]: { name: fieldValue, uid: value.uid, userId: value.userId },
      }));
    } else {
      setAdvancedSearchValues((prev) => ({
        ...prev,
        [name]: { name: value, uid: "", userId: "" },
      }));
    }
  };

  const handleAdvanceSearchDropdownChange = (e: DropdownChangeParams) => {
    setAdvancedSearchValues((prev: any) => {
      return {
        ...prev,
        [e.target.name]: e.value,
      };
    });
  };

  const handleAdvanceSearchInputChange = (event: any) => {
    const { value } = event.target;
    setAdvancedSearchValues((prev: any) => ({
      ...prev,
      [event.target.name]: value,
    }));
  };

  const handleAmountChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    const unformattedValue: number | string = currencyFormatToNumberForFilter(value ?? "");
    setAdvancedSearchValues((prevFilterObj) => ({
      ...prevFilterObj,
      [name]:
        typeof unformattedValue === "number" ? numberFormatter(unformattedValue, "en-IN", 0) : "",
    }));
  };

  /* 
    -------------------------------------------------------
      ButtonClick Functions:
    -------------------------------------------------------
  */
  const onReset = () => {
    setAdvancedSearchValues(initialAdvanceSearchObj);
    setDataTable({
      ...dataTable,
      search: "",
    });
  };

  const onSearch = () => {
    if (!validateForm()) return;

    let search = "";
    const currentSystemDate = parsedDateFromAPIValue(getLocalStorage(LOCAL_STORAGE.SYSTEM_DATE));

    if (advancedSearchValues.applicationNumber?.name) {
      search = `${search}&lanId=${advancedSearchValues.applicationNumber?.name}`;
    }
    if (advancedSearchValues.customerName?.name) {
      search = `${search}&applicantName=${advancedSearchValues.customerName?.name}`;
    }
    if (advancedSearchValues.salesManager?.name) {
      search = `${search}&salesManager.uid=${advancedSearchValues.salesManager?.uid}`;
    }
    if (advancedSearchValues.creditManager?.name) {
      search = `${search}&creditManager.uid=${advancedSearchValues.creditManager?.uid}`;
    }
    if (advancedSearchValues.product) {
      search = `${search}&product.uid=${advancedSearchValues.product}`;
    }
    if (advancedSearchValues.state?.name) {
      search = `${search}&state.uid=${advancedSearchValues.state?.uid}`;
    }
    if (advancedSearchValues.branch?.name) {
      search = `${search}&branch.uid=${advancedSearchValues.branch?.uid}`;
    }
    if (advancedSearchValues.lendingType) {
      search = `${search}&incomeSchemeProduct.name:lk=${advancedSearchValues.lendingType}`;
    }
    if (advancedSearchValues.bankFinancier?.name) {
      search = `${search}&bankFinancier.uid=${advancedSearchValues.bankFinancier?.uid}`;
    }
    if (advancedSearchValues.salesAgent?.name) {
      search = `${search}&salesAgent.uid=${advancedSearchValues.salesAgent?.uid}`;
    }
    if (currencyFormatToNumberForFilter(advancedSearchValues?.loanAmountLessThan)) {
      search = `${search}&amount:le=${currencyFormatToNumberForFilter(
        advancedSearchValues?.loanAmountLessThan
      )}`;
    }
    if (currencyFormatToNumberForFilter(advancedSearchValues?.loanAmountGreaterThan)) {
      search = `${search}&amount:ge=${currencyFormatToNumberForFilter(
        advancedSearchValues?.loanAmountGreaterThan
      )}`;
    }
    if (advancedSearchValues.loginDate) {
      search = `${search}&loginDate=${convertDateToISO(advancedSearchValues.loginDate)}`;
    }
    if (Number(advancedSearchValues.daysPendingGreaterThan) > 0) {
      const currDate = new Date(currentSystemDate);
      currDate.setDate(currDate.getDate() - Number(advancedSearchValues.daysPendingGreaterThan));
      search = `${search}&createdAtSystemDate:lt=${currDate.toISOString()}`;
    }
    if (Number(advancedSearchValues.daysPendingLessThan) > 0) {
      const currDate = new Date(currentSystemDate);
      currDate.setDate(currDate.getDate() - Number(advancedSearchValues.daysPendingLessThan));
      search = `${search}&createdAtSystemDate:gt=${currDate.toISOString()}`;
    }

    search = `${search}&qcStatus:null&loanStatus:nq=${LOAN_STATUS_ITEM.DRAFT}`;
    setDataTable({
      ...dataTable,
      search,
    });
  };

  /* 
    -------------------------------------------------------
      Helper Functions:
    -------------------------------------------------------
  */

  const updateDropdownOptions = (
    key: keyof IQCNonInitiatedAdvanceSearchDropdownList,
    options: any[]
  ) => {
    if (key === "product") {
      setProductDropdownOptions(options ?? []);
    } else if (key === "lendingType") {
      setLendingTypeDropdownOptions(options ?? []);
    }
  };

  const searchAutoCompleteLoanService = async (event: any) => {
    let suggestionsTemplate = [];
    await LoanService.getAll(0, 10, `&lanId:lk=${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteCustomerName = async (event: any) => {
    let suggestionsTemplate = [];
    await ApplicantService.getCustomerName(0, 10, `&fullName:lk=${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteSalesManager = async (event: any) => {
    let suggestionsTemplate = [];
    await UserService.getEmployee(0, 10, `${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteCreditManager = async (event: any) => {
    let suggestionsTemplate = [];
    await UserService.getEmployee(0, 10, `${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteState = async (event: any) => {
    let suggestionsTemplate = [];
    await StateService.getAll(0, 10, `name:lk=${event.query}&`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteSalesAgent = async (event: any) => {
    let suggestionsTemplate = [];
    await UserService.getEmployee(0, 10, `${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteBranch = async (event: any) => {
    let suggestionsTemplate = [];
    await OrgStructureBranchService.getAll(0, 10, `name:lk=${event.query}&`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteBank = async (event: any) => {
    let suggestionsTemplate = [];
    await BankMasterDataService.getAll(0, 10, `&name:lk=${event.query}`).then((res) => {
      if (res.status === 200) {
        suggestionsTemplate = [...res.data.results];
        // setIsLoading(false);
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  /* 
    -------------------------------------------------------
      Sectional Render Functions: For Sub components / section wise renders
    -------------------------------------------------------
  */

  const renderLoanApplicationNo = (rowData: any) => {
    const URL =
      linkBasePath +
      linkAuthRoute +
      LINK_LOAN_DETAIL +
      LINK_DDE_STAGE_AND_STATUS +
      "/" +
      rowData?.uid +
      LINK_DDE_STAGE_AND_STATUS_STAGES;

    return (
      <a className="flex items-center underline text-primary-sapphireBlue-900" href={URL}>
        {rowData?.lanId ?? "-"}
      </a>
    );
  };

  const renderLinkedApplication = (rowData: any) => {
    return <div className="flex items-center">{rowData?.linkedLoan ?? "-"}</div>;
  };

  const renderBranch = (rowData: any) => {
    return <div className="flex items-center">{rowData?.branch ?? "-"}</div>;
  };

  const renderCustomerName = (rowData: any) => {
    return (
      <div className="flex items-center" title={rowData?.applicantNam}>
        {rowData?.applicantName ?? "-"}
      </div>
    );
  };

  const renderProduct = (rowData: any) => {
    return (
      <div className="flex items-center" title={rowData?.product}>
        {rowData?.product ?? "-"}
      </div>
    );
  };

  const renderLoginDate = (rowData: any) => {
    return <div className="flex items-center">{formatDate(rowData?.loginDate) ?? "-"}</div>;
  };

  const renderDaysPending = (rowData: any) => {
    return <div className="flex items-center">{rowData?.daysSinceLogin ?? "-"}</div>;
  };

  const renderSalesManager = (rowData: any) => {
    return <div className="flex items-center">{rowData?.salesManager ?? "-"}</div>;
  };

  const itemApplicantNumberTemplate = (item: any) => {
    if (item.lanId === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.lanId}`} />
      </div>
    );
  };

  const itemCustomerTemplate = (item: any) => {
    if (item.fullName === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.fullName}`} />
      </div>
    );
  };

  const itemSalesManagerTemplate = (item: any) => {
    if (item.name === undefined || item?.name.length < 1) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemCreditManagerTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemStateTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
        <Text className="text-secondary-gray-500 text-xxs" label={item?.country} />
      </div>
    );
  };

  const itemSalesAgentTemplate = (item: any) => {
    if (item.name === undefined || item?.name.length < 1) {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemBranchTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemBankFinancierTemplate = (item: any) => {
    if (item.name === undefined || item.name === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  return (
    <div className="p-3 rounded-masterDataPageRadius bg-white flex flex-col gap-3 overflow-scroll screen-body-scroll">
      <div className="pl-3 py-2 flex items-center">
        <Text
          className="text-primary-ptext-1000 font-bold text-2xl "
          label="label.QCInitiated.heading"
        />
      </div>

      <div className="p-3 bg-primary-sapphireBlue-100 rounded-lg flex flex-col gap-6">
        <Text
          className="  text-primary-ptext-1000 font-bold text-base"
          label="label.task.advancedSearch"
        />

        <div className=" grid grid-cols-1 md:grid-cols-4 gap-3">
          <AutoComplete
            label="label.QCInitiated.applicationNumber"
            name="applicationNumber"
            field="applicationNumber"
            value={advancedSearchValues.applicationNumber?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteLoanService}
            onChange={handleAdvanceSearchAutoCompleteChange}
            itemTemplate={itemApplicantNumberTemplate}
          />
          <AutoComplete
            label="label.QCInitiated.customerName"
            name="customerName"
            field="customerName"
            variant={INPUT_STYLE_TYPE.PRIMARY}
            value={advancedSearchValues.customerName?.name}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteCustomerName}
            onChange={handleAdvanceSearchAutoCompleteChange}
            itemTemplate={itemCustomerTemplate}
          />
          <InputBox
            label="label.QCInitiated.daysPendingGreaterThan"
            name="daysPendingGreaterThan"
            value={advancedSearchValues.daysPendingGreaterThan}
            variant={INPUT_STYLE_TYPE.SECONDARY}
            onChange={handleAdvanceSearchInputChange}
            onBlur={validate}
            validation={simpleValidator?.current?.message(
              `daysPendingGreaterThan`,
              advancedSearchValues.daysPendingGreaterThan,
              "checkNumber|onlyPositive|onlyInteger"
            )}
          />
          <InputBox
            label="label.QCInitiated.daysPendingLessThan"
            name="daysPendingLessThan"
            value={advancedSearchValues.daysPendingLessThan}
            variant={INPUT_STYLE_TYPE.SECONDARY}
            onChange={handleAdvanceSearchInputChange}
            onBlur={validate}
            validation={simpleValidator?.current?.message(
              `daysPendingLessThan`,
              advancedSearchValues.daysPendingLessThan,
              "checkNumber|onlyPositive|onlyInteger"
            )}
          />
          <AutoComplete
            label="label.QCInitiated.salesManager"
            name="salesManager"
            field="salesManager"
            value={advancedSearchValues.salesManager?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteSalesManager}
            onChange={handleAdvanceSearchAutoCompleteChange}
            itemTemplate={itemSalesManagerTemplate}
          />
          <AutoComplete
            label="label.QCInitiated.creditManager"
            name="creditManager"
            field="creditManager"
            value={advancedSearchValues.creditManager?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteCreditManager}
            onChange={handleAdvanceSearchAutoCompleteChange}
            itemTemplate={itemCreditManagerTemplate}
          />
          <InputBox
            label="label.QCInitiated.loanAmountGreaterThan"
            name="loanAmountGreaterThan"
            value={advancedSearchValues.loanAmountGreaterThan}
            variant={INPUT_STYLE_TYPE.SECONDARY}
            onChange={handleAmountChange}
          />
          <InputBox
            label="label.QCInitiated.LoanAmountLessThan"
            name="loanAmountLessThan"
            value={advancedSearchValues.loanAmountLessThan}
            variant={INPUT_STYLE_TYPE.SECONDARY}
            onChange={handleAmountChange}
          />
          <SimpleDropDown
            variant={DROPDOWN_STYLE_TYPE.SECONDARY}
            label="label.QCInitiated.product"
            name="product"
            options={productDropdownOptions}
            optionLabel="name"
            optionValue="uid"
            value={advancedSearchValues.product}
            onChange={handleAdvanceSearchDropdownChange}
          />
          <AutoComplete
            label="label.QCInitiated.state"
            name="state"
            field="state"
            value={advancedSearchValues.state?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteState}
            onChange={handleAdvanceSearchAutoCompleteChange}
            itemTemplate={itemStateTemplate}
          />
          <AutoComplete
            label="label.QCInitiated.salesAgent"
            name="salesAgent"
            field="salesAgent"
            value={advancedSearchValues.salesAgent?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteSalesAgent}
            onChange={handleAdvanceSearchAutoCompleteChange}
            itemTemplate={itemSalesAgentTemplate}
          />
          <SystemDatePicker
            variant={INPUT_STYLE_TYPE.PRIMARY}
            label="label.QCInitiated.LoginDate"
            placeholder="DD-MMM-YYYY"
            name="loginDate"
            value={advancedSearchValues.loginDate}
            onChange={handleAdvanceSearchInputChange}
          />
          <AutoComplete
            label="label.QCInitiated.branch"
            name="branch"
            field="branch"
            variant={INPUT_STYLE_TYPE.PRIMARY}
            value={advancedSearchValues.branch?.name}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteBranch}
            onChange={handleAdvanceSearchAutoCompleteChange}
            itemTemplate={itemBranchTemplate}
          />
          <SimpleDropDown
            label="label.QCInitiated.lendingType"
            variant={DROPDOWN_STYLE_TYPE.SECONDARY}
            name="lendingType"
            options={lendingTypeDropdownOptions}
            optionLabel="name"
            optionValue="name"
            value={advancedSearchValues.lendingType}
            onChange={handleAdvanceSearchDropdownChange}
          />
          <AutoComplete
            label="label.QCInitiated.bank/Financier"
            name="bankFinancier"
            field="bankFinancier"
            value={advancedSearchValues.bankFinancier?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteBank}
            onChange={handleAdvanceSearchAutoCompleteChange}
            itemTemplate={itemBankFinancierTemplate}
          />

          <div className="flex justify-end">
            <div className="flex items-center gap-10">
              <ButtonBox buttonType={BUTTON_TYPE.RESET_OUTLINE} onClick={onReset} />
              <ButtonBox buttonType={BUTTON_TYPE.SEARCH_OUTLINE} onClick={onSearch} />
            </div>
          </div>
        </div>
      </div>

      <div className="p-3 flex w-full justify-between items-center">
        <Text
          className="text-primary-ptext-1000 font-bold text-2xl "
          label="label.QCInitiated.myApplication"
        />
      </div>

      <ReadOnlyTable
        standardDataFormatPromise={LoanService.getAll}
        dataKey="id"
        search={dataTable.search}
        ref={dataTableRef}
      >
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.partnerTask.loanApplicationNo")}
          body={renderLoanApplicationNo}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.partnerTask.linkedApplication")}
          body={renderLinkedApplication}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.branch")}
          body={renderBranch}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.partnerTask.customerName")}
          body={renderCustomerName}
          style={{ minWidth: "200px", width: "auto" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.loanListing.product")}
          body={renderProduct}
          style={{ minWidth: "200px", width: "auto" }}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.QCInitiated.LoginDate")}
          body={renderLoginDate}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6"
          header={useTranslateText("label.QCInitiated.daysPending")}
          body={renderDaysPending}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs py-3 pl-6 pr-3"
          header={useTranslateText("label.loanListing.salesManager")}
          body={renderSalesManager}
          style={{ minWidth: "150px", width: "auto" }}
        />
      </ReadOnlyTable>

      <ToastCustom
        position={position}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={toastMessageVariant}
      />

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default PageQCNonInitiated;
